import React from 'react';
import { render } from 'react-dom';
import Gallery from 'react-grid-gallery';

// https://github.com/benhowell/react-grid-gallery

const IMAGES =
[
{
    src: "http://fakeimg.pl/360x360/000/?text=1",
    thumbnail: "http://fakeimg.pl/360x360/000/?text=1",
    thumbnailWidth: 320,
    thumbnailHeight: 320,
},
]

class Container extends React.Component{
    render(){
        return(
            <div>
                <Gallery images={IMAGES} rowHeight={320} enableImageSelection={false}/>
            </div>
        )
    }
}

render(
        <Container/>,
        document.getElementById('galery')
);
